.container {
    display: flex;
    flex-direction: column;
    background-color: var(--tg-theme-bg-color);
    border-radius: 10px;
}

.serviceContainer {
    position: relative;
    display: flex;
    /* padding-bottom: 1px; */
}

.serviceInput {
    background-color: var(--tg-theme-bg-color);
    /* background-color: aqua;  */
    border-radius: none;
    height: 44px;
    border: none;
    color: var(--tg-theme-text-color);
    font-size: 17px;
    line-height: 22px;
    letter-spacing: -0.43px;
    font-family: 'SF Pro Regular';
    outline: none;



    padding-left: 16px;

    width: 100%;
    box-sizing: border-box;
    max-width: 100%;
}

.serviceInput::placeholder {
    color: var(--tg-theme-hint-color);
    font-size: 17px;
    line-height: 22px;
    letter-spacing: -0.43px;
    font-family: 'SF Pro Regular';
}

.serviceName {
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
}

.servicePrice {
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;    
}

.borderBottom {
    position: absolute;
    bottom: 0;
    left: 16px;
    right: 0;
    border-bottom: 1px solid var(--tg-theme-secondary-bg-color);

    /* border-bottom: 1px solid black; */
    pointer-events: none;
}



.dropdown {
    position: absolute;
    background-color: var(--tg-theme-bg-color);    
    z-index: 10000;
    top: 110%;
    left: 16px;
    right: 17px;    
    border-radius: 10px;
    padding: 0;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1), 0 6px 20px rgba(0, 0, 0, 0.1);
}

.dropdownItem {
    border-bottom: 1px solid var(--tg-theme-secondary-bg-color);    
    display: flex;
    height: 44px;
    margin-left: 16px;
    align-items: center;
    color: var(--tg-theme-text-color);
    
}

.noBorderBottom {
    border: none;
}

.darkBorder {
    border: 1px solid var(--tg-theme-secondary-bg-color);
}