.btnContainer {
    margin: 0 -15px;
    margin-top: 10px;
}

.btn {
    
    display: flex;
    align-items: center;
    background-color: var(--tg-theme-bg-color);
    
    height: 44px;
    border: none;
    
    font-size: 17px;
    line-height: 22px;
    letter-spacing: -0.43px;
    font-family: 'SF Pro Regular';

    padding-left: 16px;

    width: 100%;
    box-sizing: border-box;
    max-width: 100%;
    color: var(--tg-theme-link-color);
}

.addIcon {
    margin-right: 25px;
}