.container {
    position: relative;
}

.selectContainer {
    height: 44px;
    background-color: var(--tg-theme-bg-color);
    /* background-color: aqua; */
    
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 16px;

    margin-bottom: 10px;
}

.nameTemplateAndIconContainer {
    display: flex;
    align-items: center;
}

.icon {
    margin-left: 5px;
}

.dropdownContainer {
    border-radius: 10px;
    margin: 0 15px;
    background-color: var(--tg-theme-bg-color);
    /* background-color: aqua; */
    position: absolute;
    z-index: 100;
    width: calc(100% - 30px);

    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1), 0 6px 20px rgba(0, 0, 0, 0.1);
}

.dropdownItem {
    display: flex;
    align-items: center;
    height: 44px;
    padding-left: 16px;
    position: relative;
}

.borderBottom {
    position: absolute;
    bottom: 0;
    left: 15px; 
    right: 0;
    border-bottom: 1px solid var(--tg-theme-secondary-bg-color);
    /* border-bottom: 1px solid black; */
}