.containerWithoutHeader {
    margin: 0 -15px;
}

.inpt {
    
    background-color: var(--tg-theme-bg-color);
    height: 44px;
    border: none;
    color: var(--tg-theme-text-color);
    font-size: 17px;
    line-height: 22px;
    letter-spacing: -0.43px;
    font-family: 'SF Pro Regular';
    outline: none;
    padding-left: 16px;
    padding-right: 16px;
    
    width: 100%;
    box-sizing: border-box;
    max-width: 100%;
}


.inpt::placeholder {
    color: var(--tg-theme-hint-color);
    font-size: 17px;
    line-height: 22px;
    letter-spacing: -0.43px;
    font-family: 'SF Pro Regular';
}

.gapContainer {
    margin-top: 10px;
    /* margin-bottom: -5px; */
}

.btn {
    display: flex;
    align-items: center;
    background-color: var(--tg-theme-bg-color);
    height: 44px;
    border: none;
    
    font-size: 17px;
    line-height: 22px;
    letter-spacing: -0.43px;
    font-family: 'SF Pro Regular';

    padding-left: 16px;

    width: 100%;
    box-sizing: border-box;
    max-width: 100%;
}

.addBtn {
    justify-content: space-between;
    color: var(--tg-theme-link-color);
    margin-bottom: 10px;
}

.deleteBtn {
    color: #FF4530;
}

.deleteIcon {
    margin-right: 25px;
}