.container {
    margin: 0 -15px;
}

.btn {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;

    height: 44px;
    background-color: var(--tg-theme-bg-color);
    
    
    border: none;
    width: 100%;

    color: var(--tg-theme-text-color);

    padding-left: 15px;
    padding-right: 16px;
}

.btnText {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 95%;
}

.borderBottom {
    position: absolute;
    bottom: 0;
    left: 16px;
    right: 0;
    border-bottom: 1px solid var(--tg-theme-secondary-bg-color);
    pointer-events: none;
} 