.container {
    /* position: relative; */
    width: 50px;
    width: 100%;
    overflow: hidden;
    /* z-index: 100; */
    margin-right: 5px;
}

.slowCondition {
    transition: transform 600ms cubic-bezier(0.13, 0.67, 0.01, 0.94);

}

.fastCondition {
    transition: transform 700ms cubic-bezier(0.13, 0.67, 0.01, 0.94);
}

@keyframes fade-in {
    0% {
       transform: translateY(150px);
       opacity: 0;
    }
    100% {
       transform: translateY(0);
       opacity: 1;
    }
 }

.cellHour {
    width: 100%;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    user-select: none;
    transition: all 100ms linear;
}

.cellIner {
    width: fit-content;
    height: 100%;
    transition: all 100ms linear;
    cursor: pointer;
    border-radius: 7px;
    line-height: 35px;
    text-align: center;
    display: flex;
    justify-content: end;
    align-items: center;
    font-size: 14px;
    color: #666;
    padding: 0 10px;
}

.cellIner:hover {
    background-color: #ff9d0ac9;
    color: white;
}

.selected {
    /* font-weight: 500; */
    color: blue;
    background-color: red;
    font-size: 16px;
}

.hidden {
    opacity: 0;
    background-color: red;
    visibility: hidden;
    pointer-events: none;
}