.container {
    margin-bottom: 15px;
    
}

.inptContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 44px;
    padding-left: 16px;
    padding-right: 16px;
    background-color: var(--tg-theme-bg-color);
    position: relative;
}

.inpt {
    background-color: var(--tg-theme-bg-color);
    
    border: none;
    color: var(--tg-theme-link-color);
    font-size: 17px;
    line-height: 22px;
    letter-spacing: -0.43px;
    font-family: 'SF Pro Regular';
    outline: none;

}

.btn {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: var(--tg-theme-bg-color);
    color: #FF3B30;
    /* background-color: aqua; */
    height: 44px;
    border: none;
    
    font-size: 17px;
    line-height: 22px;
    letter-spacing: -0.43px;
    font-family: 'SF Pro Regular';

    padding-left: 16px;

    width: 100%;
    box-sizing: border-box;
    max-width: 100%;
}

.borderBottom {
    position: absolute;
    bottom: 0;
    left: 16px;
    right: 0;
    border-bottom: 1px solid var(--tg-theme-secondary-bg-color);

    /* border-bottom: 1px solid black; */
    pointer-events: none;
}